.div-main {
  width: 400px;
  background-color: cadetblue;
  margin: 50px auto;
  padding: 10px;
  min-height: 500px;
}

.form-note input {
  background-color: darkblue;
  border: 0;
  width: 300;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 10px;
  color: #ffffff;
}

.form-note input::placeholder {
  color: rgb(255, 255, 255, 0.5);
}

.form-note input:focus {
  outline: none;
}

.form-note button:focus {
  outline: none;
}

.form-note button {
  height: 50px;
  width: 70px;
  border-radius: 10px;
  border: 0;
  background-color: rgb(110, 40, 7);
  cursor: pointer;
  color: #ffffff;
}
