.div-note {
  background-color: darkgreen;
  width: 350px;
  margin: 10px auto;
  border: 2px solid green;
  border-radius: 5px;
}

.div-note p {
  padding: 5px;
  position: relative;
}

.div-note p span {
  position: absolute;
  right: 10px;
  cursor: pointer;
}

.faicons {
  margin: 0 5px;
  font-size: 15px;
}

.div-note p input {
  background-color: transparent;
  border: 0;
  color: #ffffff;
  font-size: 18px;
}

.div-note p input:focus {
  outline: none;
}

